.navbar {
  height: 55px;
  box-shadow: 0 3px 6px var(--shadow-color);
  background-color: var(--background-color);
  color: var(--text-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 998;
  .logo {
    font-size: 1.5em;
    font-weight: bolder;
    color: var(--primary-color);
    text-shadow: 0 1px 3px var(--shadow-color);
    span {
      color: var(--text-color);
    }
  }
  .nav-links {
    a {
      margin-left: 1em;
      color: var(--text-light);
      letter-spacing: 2px;
      text-transform: uppercase;
      position: relative;
      font-size: 0.9em;
      &::before {
        content: "";
        display: inline-block;
        height: 3px;
        width: 100%;
        background-color: var(--primary-color);
        position: absolute;
        bottom: -5px;
        left: 0;
        transform: scaleX(0);
        transition: getAnimationDuration(0.2s) ease-in-out;
      }
      &:hover {
        &::before {
          transform: scaleX(1);
        }
      }
      &.active {
        color: var(--primary-color);
        font-weight: bold;
      }
    }
  }
  .sidebar-btn {
    height: 27px;
    width: 30px;
    position: relative;
    display: none;
    .bar {
      border-radius: 5px;
      height: 5px;
      width: 100%;
      background-color: var(--text-color);
      position: absolute;
      transition: transform getAnimationDuration(0.5s);
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform-origin: right;
        transform: scaleX(0.8) translateY(-50%);
      }
      &:nth-child(3) {
        top: 100%;
        transform-origin: right;
        transform: scaleX(1.1) translateY(-100%);
      }
    }
    &.active {
      .bar {
        transform-origin: center;
        top: 50%;
        &:nth-child(1) {
          transform: translate(0, -50%) scale(1) rotate(45deg);
        }
        &:nth-child(2) {
          transform: scaleX(0) translateY(-50%);
        }
        &:nth-child(3) {
          transform: translate(0, -50%) scale(1) rotate(-45deg);
        }
      }
    }
  }
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 260px;
  z-index: 999;
  background-color: var(--background-color);
  box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0.2), 3px 0 5px var(--shadow-color);
  animation: getAnimationDuration(0.3s) slideInLeft;
  .sidebar-link {
    display: block;
    padding: 0.5em 1em;
    border-left: 0.4em solid transparent;
    margin-bottom: 1em;
    color: var(--text-color);
    font-size: 1.1em;
    transform: translateX(-100%);
    animation: getAnimationDuration(0.3s) slideInLeft forwards;
    @for $i from 1 through 5 {
      &:nth-child(#{$i}) {
        animation-delay: #{$i * 0.1}s;
      }
    }
    &.active {
      border-left-color: var(--primary-color);
    }
    &:hover {
      border-left-color: var(--primary-color);
      background-color: rgba(0, 0, 0, 0.05);
    }
    svg {
      width: 1.2em;
      margin-right: 0.5em;
    }
  }
}

.settings-btn {
  margin-left: 1em;
  color: var(--text-light);
  letter-spacing: 2px;
  text-transform: uppercase;
  position: relative;
  font-size: 0.9em;
  cursor: pointer;
  .drop-down {
    position: absolute;
    display: inline-block;
    top: 1vh;
    left: -3.4vw;
    height: 2em;
    width: 16.5em;
  }
  &::before {
    content: "";
    display: inline-block;
    height: 3px;
    width: 100%;
    background-color: var(--primary-color);
    position: absolute;
    bottom: -5px;
    left: 0;
    transform: scaleX(0);
    transition: getAnimationDuration(0.2s) ease-in-out;
  }
  &:hover {
    &::before {
      transform: scaleX(1);
    }
  }
}
.settings-btn-sidebar {
  padding: 0.5em 1em;
  border-left: 0.4em solid transparent;
  margin-bottom: 1em;
  color: var(--text-color);
  font-size: 1.1em;
  position: relative;
  cursor: pointer;
  .drop-down {
    position: absolute;
    display: inline-block;
    top: 1vh;
    left: -1vw;
    height: 2em;
    width: 14em;
    .option {
      min-height: 1.5em;
      min-width: 1.5em;
      box-shadow: 0 3px 6px var(--shadow-color);
      border-radius: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        border-radius: 50%;
        box-shadow: 0 10px 10px var(--shadow-color);
      }
    }
  }
}
