:root {
  --background-color: #fff;
  --background-light: #fff;
  --shadow-color: rgba(0, 0, 0, 0.2);
  --primary-color: rgba(232, 67, 147, 1);
  --text-color: #0a0a0a;
  --text-light: #575757;
  --font-size: 16px;
  --animation-speed: 1;
}

@function getAnimationDuration($duration: number) {
  @return calc($duration * var(--animation-speed));
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: var(--font-size);
  font-family: "Roboto", sans-serif;
  padding-top: 55px;
  background-color: var(--background-light);
}

a {
  text-decoration: none;
}

img {
  object-fit: cover;
}

ul {
  list-style: none;
}

.btn {
  border: none;
  cursor: pointer;
  outline: none;
  background: linear-gradient(35deg, #34495e, var(--primary-color));
  color: #fff;
  padding: 0.75em 1em;
  text-transform: uppercase;
  font-size: 1.1em;
  font-weight: bold;
  letter-spacing: 1px;
  border-radius: 3px;
  box-shadow: 0 2px 6px var(--shadow-color);
  &:hover {
    box-shadow: 6px 6px 10px var(--shadow-color);
    transition: 0.2s ease-in-out;
  }
}

.custom-image {
  height: 0;
  width: 100%;
  position: relative;
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.section {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 3vw;
  &.disp-block {
    display: block;
  }
  .title {
    color: var(--text-color);
    font-size: 2.5em;
    margin-bottom: 0.25em;
    font-weight: 900;
  }
  .info {
    color: var(--text-light);
    font-size: 1em;
    letter-spacing: 1px;
    line-height: 1.3;
    margin-bottom: 1.25em;
  }
  .col {
    width: 50%;
  }
}

@import "partials/navbar";
@import "partials/hero-section";
@import "partials/improve-section";
@import "partials/quote-section";
@import "partials/carousel-section";
@import "partials/footer";
@import "partials/recipes-section";
@import "partials/recipe";
@import "partials/meal-plan";
@import "partials/about";
@import "partials/newsletter-modal";
@import "partials/category";
@import "partials/search";

.options-container {
  box-shadow: 0 5px 10px var(--shadow-color);
  padding: 1em;
  border-radius: 5px;
  border-left: 0.5em solid var(--primary-color);
  background-color: var(--background-light);
  margin-top: 1em;
  display: flex;
  gap: 1em;
  .option {
    min-height: 2em;
    min-width: 2em;
    box-shadow: 0 3px 6px var(--shadow-color);
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      transition: 0.2s ease-in-out;
      border-radius: 50%;
      box-shadow: 0 10px 10px var(--shadow-color);
    }
  }
  .btn span {
    display: inline-block;
    margin-left: 0.5em;
  }
}
.disp-block h2 {
  font-size: 1.75em;
  color: var(--text-color);
}

.main {
  padding-block: 3vw;
}

.container {
  padding-inline: 25vw;
}

@import "partials/animations.scss";

@media screen and (max-width: 1280px) {
  .container {
    padding-inline: 18vw;
  }
}
@media screen and (max-width: 1050px) {
  .container {
    padding-inline: 12vw;
  }
}

@media screen and (max-width: 768px) {
  .navbar {
    .nav-links {
      display: none;
    }
    .settings-btn {
      display: none;
    }
    .sidebar-btn {
      display: block;
    }
  }
  .section {
    .col {
      width: 100%;
      padding: 0 !important;
      text-align: center;
      margin-bottom: 2em;
    }
  }
  .skill-item {
    display: inline-block;
    margin: 0.75em;
  }

  .section.improve-skills .btn {
    display: block;
    margin-left: 50%;
    transform: translateX(-50%);
  }

  .chef-card {
    box-shadow: 0 5px 10px var(--shadow-color);
  }
  .container {
    padding-inline: 6vw;
  }
  .footer {
    grid-template-columns: repeat(4, 1fr);
    .footer-section:nth-child(1) {
      grid-column: span 4;
    }
    .footer-section:nth-child(2),
    .footer-section:nth-child(3) {
      grid-column: span 2;
    }
  }
}

@media screen and (max-width: 480) {
  .container {
    padding-inline: 3vw;
  }
}
